
document.addEventListener('DOMContentLoaded',function(){
    eventListeners();
    darkMode();
    limitarTexto('.anuncio .contenido-anuncio p', 30);
});

function darkMode(){
    const prefiereDarkMode = window.matchMedia('(prefers-color-schema: dark)');
    prefiereDarkMode.addEventListener('change', function(){
        if ( prefiereDarkMode.matches ){
            document.body.classList.add('dark-mode');
        }else{
            document.body.classList.remove('dark-mode');
        }

    });
    const botonDarkMode = document.querySelector('.dark-mode-boton');
    botonDarkMode.addEventListener('click', function(){
        document.body.classList.toggle('dark-mode');
    });
}

function eventListeners() {
    const mobileMenu = document.querySelector('.mobile-menu');
    mobileMenu.addEventListener('click', navegacionResponsive);
    //Muestra campos condicionales
    const metodoContacto = document.querySelectorAll('input[name="contacto[contacto]"]');
    metodoContacto.forEach(input => input.addEventListener('click', mostrarMetodosContacto));
}

function navegacionResponsive(){
    const navegacion = document.querySelector('.navegacion');
    navegacion.classList.toggle('mostrar');
}

function limitarTexto(selector, maxLength) {
    var elementos = document.querySelectorAll(selector);
  
    elementos.forEach(function(elemento) {
      var textoCompleto = elemento.textContent.trim();
      var textoRecortado = textoCompleto.slice(0, maxLength);
  
      if (textoCompleto.length > maxLength) {
        elemento.textContent = textoRecortado + "...";
      }
    });
}

function mostrarMetodosContacto(e){
    const contactoDiv = document.querySelector('#contacto');
    if ( e.target.value === 'telefono' ){
        contactoDiv.innerHTML = `
            <label for="telefono">Número: </label>
            <input id="telefono" type="tel" placeholder="Tu teléfono" name="contacto[telefono]">
            <p>Elija la fecha y hora para la llamada</p>
            <label for="fecha">Fecha: </label>
            <input type="date" id="fecha" name="contacto[fecha]">

            <label for="hora">Hora: </label>
            <input type="time" id="hora" min="09:00" max="18:00" name="contacto[hora]">
        `;
    }else{
        contactoDiv.innerHTML = `
            <label for="email">Email: </label>
            <input id="email" type="email" placeholder="Tu Email" name="contacto[email]" required>
        `;
    }
}

